.navigation-button {
    position: absolute;
    width: 50px;
    height: 50px;
    border-radius: 100%;
    border-style: solid;
    border-width: 1px;
    transition-property: background-color;
    transition-duration: 0.4s;
    cursor: pointer;
}
.navigation-button:hover {
    background-color: rgb(128, 128, 128);
}