#d3 {
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url("./bg.png");
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    width: 100%;
    height: 100%;
    position: absolute;
    animation-duration: 0.4s;
    animation-fill-mode: forwards;
}
#d3-planet-img {
    height: 50%;
    aspect-ratio: 1;
    border-radius: 20px;
    position: absolute;
}
#d3-planet-name {
    position: absolute;
    font-family: outfit-medium;
    font-size: 50px;
    padding: 20px;
    padding-left: 150px;
    padding-right: 150px;
    top: 180px;
    background-color: rgba(217, 217, 217,15%);
    border-radius: 100px;
    color: white;
}
#d3-planet-text-1 {
    top: 60px;
    left: 60px;
    width: 600px;
    height: 350px;
    background-color: rgba(217, 217, 217, 25%);
    border-radius: 50px;
    position: absolute;
}
