#d1 {
    position: absolute;
    height: 500px;
    width: 1000px;
    background-image: url('./bg.png');
    background-size: 100%;
    background-position: center center;
    background-repeat: no-repeat;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    animation-duration: 0.4s;
    animation-fill-mode: forwards;
}
#d2 {
  position: relative;
  height: 83%;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  background-color: rgba(146, 146, 146, 0.219);
  backdrop-filter: none;
  border-radius: 70px;
}
#title {
  color: white;
  background-color: rgba(146, 146, 146, 0.219);
  position: absolute;
  z-index: 1;
  top: 0px;
  height: 100px;
  width: 500px;
  border-radius: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  backdrop-filter: blur(5px);
}
#title-text {
  font-family: outfit-bold;
  font-size: 50px;
}
#bio {
  color: white;
  font-family: outfit-extra-light;
}
#text-container {
  display: flex;
  flex-direction: column;
}
#buttons {
  position: absolute;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  background-color: rgba(146, 146, 146, 0.493);
  width: 95%;
  height: 80px;
  bottom: 25px;
  border-radius: 100vh;
  flex-direction: row;
  backdrop-filter: blur(5px);
}
@keyframes animation-vanish {
  from {
    opacity: 100%;
  }
  to {
    opacity: 0%;
  }
}
@keyframes animation-flash {
  from {
    opacity: 0%;
  }
  to {
    opacity: 100%;
  }
}
