.btn {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #d9d9d9;
    border-radius: 1000px;
    min-width: 100px;
    height: 70%;
    padding-left: 20px;
    padding-right: 20px;
    cursor: pointer;
    transition-duration: 0.2s;
    font-family: outfit-medium;
    font-size: 30px;
}
.btn:hover {
    background-color: rgb(170, 170, 170);
}