#d1-2 {
    width:  100%;
    height: 100%;
    position: absolute;
    background-image: url('./bg.png');
    background-size: cover;
    background-position: center center;
    animation-duration: 0.4s;
    justify-content: center;
    align-items: center;
    animation-fill-mode: forwards;
}
#circle {
    height: 500px;
    width: 500px;
    border-radius: 100%;
    background-color: rgba(217, 217, 217, 0.28);
    display: flex;
    justify-content: center;
    align-items: center;
}
#circle-span {
    font-size: 150px;
    color: white;
    height: 300px;
    font-family: manjari;
}
#d1-2-1 {
    background-color: rgba(217, 217, 217, 0.11);
    width: 50%;
    height: 120px;
    border-radius: 71px;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 700px;
}
#d1-2-1-buttons {
    background-color: rgba(217, 217, 217, 0.4);
    height: 70%;
    width: 90%;
    border-radius: 100px;
    backdrop-filter: blur(20.2px);
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}