#d2-1 {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
}
#d2-1-img {
    height: 100%;
    width: 80%;
}
#d2-1-1 {
    background-color: rgba(217, 217, 217, 0.11);
    height: 80%;
    width: 70%;
    border-radius: 71px;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
}
#d2-1-title {
    position: absolute;
    background-color: rgba(217, 217, 217, 15%);
    width: 30%;
    height: 11.5%;
    display: flex;
    justify-content: center;
    align-items: center;
    top: 4%;
    border-radius: 100px;
}
#d2-1-title-span {
    position: relative;
    padding-right: 10px;
    padding-left: 10px;
    font-family: outfit-bold;
    font-size: clamp(15px,2vw,80px);
    color: white;
}
#d2-1-container {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    animation-fill-mode: forwards;
    animation-duration: 0.4s;
}
#d2-1-1-1 {
    height: 60%;
    aspect-ratio: 1;
    background-color: rgba(217, 217, 217, 22%);
    border-radius: 50px;
    flex-direction: column;
    display: flex;
    justify-content: center;
    align-items: center;
}