.back {
    height: 50px;
    width: 50px;
    border-radius: 100%;
    background-color: white;
    position: absolute;
    top: 20px;
    left: 20px;
    font-size: 20px;
    border-style: solid;
    border-width: 1px;
    transition-property: background-color;
    transition-duration: 0.2s;
    cursor: pointer;
}
.back:hover {
    background-color: rgba(128, 128, 128, 1);
}